.MealPage {
    min-height: 100vh;
    background-color: #fff;
    position: relative;
    overflow: hidden;
    color: rgb(58, 56, 56);
    &__circle {
        width: 115vw;
        height: 115vw;
        background-color: $main-color;
        border-radius: 50%;
        position: absolute;
        top: -15%;
        right: -25%;
    }
    &__plateImage {
        width: 75%;
        z-index: 10;
        position: absolute;
        left: 50%;
        top: 10%;
        transform: translateX(-50%);
    }
    &__backIcon {
        position: absolute;
        top: 3%;
        left: 5%;
        font-size: 1.5rem;
    }
    &__content {
        margin-top: 400px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__macroBox {
        margin-top: 20px;
        width: 100%;
        height: 16vh;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
    }
    &__macro {
        width: 20%;
        height: 90%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        border-radius: 20px;
        box-shadow: 0 0 15px rgba(0, 0, 0, 0.212);
         background-color: rgba(241, 233, 233, 0.39);
    }
    &__macro-value {
        font-weight: 600;
        text-align: center;
        font-size: 1.2rem;
        margin-bottom: 10px;
        color: black;
    }
    &__macro-label {
        text-align: center;
        margin-bottom: 4px;
        font-weight: 500;
    }
    &__macro-unit {
        text-align: center;
        color: rgba(0, 0, 0, 0.336);
    }
}